

.blog {
  width: 100%;
  background: #fff;
  display: flex;
  padding-bottom: 241px;
  flex-direction: column;
  overflow: hidden;
  font-family: Inter, sans-serif;
  color: #005880;
  font-weight: 800;
  justify-content: center;
  align-items: center;
} 

.header {
  width: 100%;
  font-size: 50px;
  color: #fff;
  white-space: nowrap;
  padding: 20px;
  text-align: center;
  margin-bottom: 100px;
  box-sizing: border-box;
}

@keyframes slideImages {

  0%,
  33% {
    opacity: 1;
  }

  34%,
  100% {
    opacity: 0;
  }
}

.headerSection {
  position: relative;
  width: 100%;
  height: 80vh;
  overflow: hidden;
}

.headerTitle {
  position: relative;
  margin-top: 5vh;
  font-size: 60px;
  color: #005880;
}

.headerContent {
  position: relative;
  text-transform: uppercase;
  color: #005880;
  font-weight: 600;
  font-size: 14px;
  margin-top: 20px;
  text-align: center;
  margin: 5px auto;
  max-width: 100%;

}

.categoriesTitle {
  font-size: 28px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 60px;
}

.mainContent {
  display: flex;
  margin-top: 25px;
  width: 100%;
  flex-direction: column;
  align-items: start;
  font-size: 16px;
  font-weight: 700;
  padding: 0 63px;
}


.categoryContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px 0;
}

.categoryButton {
  background-color: #005880;
  color: white;
  font-size: 14px;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  padding: 10px 10px;
  cursor: pointer;
  text-align: center;
  white-space: nowrap;
  transition: background-color 0.3s ease;
}

.categoryButton:hover {
  background-color: #0090d8;
}

.categoryButton:active {
  background-color: #003344;
}

.selectedCategory {
  background-color: #003344;
}

.selectedCategory:hover {
  background-color: #014869;
}

.categoryTag {
  width: 100%;
  border-radius: 5px;
  background-color: #005880;
  padding: 15px 15px;
  justify-content: center;
  align-items: center;
}

.article {
  margin-top: 10px;
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.noarticles {
  color: #005880;
  width: 100%;
  font-size: 20px;
  text-align: center;
  padding-top: 100px;
  text-transform: capitalize;
}

.articleImage {
  display: flex;
  object-position: center;
  justify-content: center;
  align-items: center;
  width: 85%;
  border-radius: 30px;
  margin: 20px;
  height: 65vh;
  box-shadow: 8px 8px 5px rgb(66, 101, 122);
  transition: transform 0.3s ease;
}

.articleImage:hover {
  transform: scale(1.02);
}

.articleTitle {
  font-size: 32px;
  text-transform: uppercase;
  margin: 20px 0;
  padding-left: 100px;
  padding-right: 100px;
  color: #003344;
}

.articleMeta {
  display: flex;
  flex-direction: row;
  gap: 10px;
  font-weight: 400;
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 60px;
  padding-right: 50px;
  align-items: center;
}

.articleDate,
.articleAuthor {
  gap: 10px;
}

.articleAuthor {
  font-size: 12px;
  justify-content: center;

}

.articleContent {
  font-weight: 500;
  font-size: 16px;
  margin: 20px 0;
  white-space: pre-wrap;
  padding-left: 100px;
  padding-right: 100px;
  text-align: justify;
  color: #005880;
  line-height: 20px;
}

.seeMoreButton {
  background-color: #005880;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  text-align: left;
  display: block;
  margin: 0;
  transition: transform 0.2s;
}

.seeMoreButton:hover {
  background-color: #0090d8;
  transform: scale(1.2);
}

.seeMoreButton:active {
  background-color: #003344;
}


.pagination {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}

.pageButton {
  padding: 8px 12px;
  margin: 0 5px;
  border: 1px solid #005880;
  background-color: #fff;
  color: #005880;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s, color 0.3s;
}

.pageButton:hover {
  background-color: #005880;
  color: #fff;
}

.pageButton:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.active {
  background-color: #005880;
  color: #fff;
  border-color: #005880;
}

/*------------------------RESPONSIVE------------------------------------*/
@media (max-width: 768px) {

  .header {
    min-height: 200px;
    font-size: 24px;
    margin-bottom: 30px;
  }

  .headerTitle {
    margin-top: 100px;
    font-size: 1.8rem;
  }

  .headerContent {
    opacity: 0;

  }

  .headerImage {
    position: absolute;
    height: 40%;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  .categoriesTitle {
    font-size: 24px;
    margin: 5px;
    padding-top: 70px;
  }

  .categoryContainer {
    display: grid;
    grid-template-columns: auto auto;
    border-radius: 2px;
    justify-content: center;
    align-items: center;
  }

  .categoryContainer>* {
    font-size: 10px;
    padding: 8px 8px;
    margin: 5px 10px;
    justify-content: center;
  }

  .articleTitle {
    font-size: 20px;
    margin: 10px 0;
  }

  .articleImage {
    border-radius: 20px;
    width: 95%;
    margin: 15px 15px;
    height: auto;
  }

  .articleDate,
  .articleAuthor {
    width: 100%;
    display: flex;
    flex-direction: column;
    font-size: 10px;
  }

  .articleMeta {
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-weight: 400;
    width: 100%;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 10px;
    align-items: center;
  }

  .articleContent {
    font-size: 14px;
    padding: 0 20px;
  }

  .seeMore {
    padding: 8px 16px;
    margin: 10px;
  }
}

/* Mobile (480px and below) */
@media (max-width: 480px) {
  .header {
    min-height: 300px;
    font-size: 24px;
    margin-bottom: 30px;
  }

  .headerTitle {
    margin-top: 150px;
    font-size: 1.8rem;
  }

  .categoriesTitle {
    font-size: 20px;
  }

  .categoryContainer {
    gap: 2px;
    padding: 0 0px;
    display: grid;
    grid-template-columns: auto auto;
    border-radius: 2px;
    justify-content: center;
    align-items: center;
  }

  .categoryContainer>* {
    font-size: 10px;
    padding: 8px 8px;
  }

  .articleTitle {
    font-size: 14px;
    margin: 20px 0;
    padding-left: 20px;
    padding-right: 30px;
  }

  .articleContent {
    font-size: 12px;
  }

  .seeMore {
    font-size: 12px;
    padding: 6px 12px;
  }
}